import React, { Component } from 'react'
import { Avatar } from '../common/Common.js'
import axios from '../../api/axios.js'
import url from '../../api/url.js'
import s from './AddFollow.css'
import Cookies from 'js-cookie'
import { connect } from 'react-redux'
import actions from '../../actions'
import { login } from '../../api/login.js'

class FollowCard extends Component {
  constructor(props) {
    super(props)

    this.handleFollow = this.handleFollow.bind(this)
  }
  async handleFollow () {
    let { sum, setFollowSum } = this.props
    let { starId, is_following, changeIsFollow } = this.props
    if (sum >= 3 && !is_following) return
    let user_id = Cookies.get('user_id')
    let { code, data } = await axios({
      method: 'put',
      url: is_following ? url.userUnFollowing(user_id) : url.userFollowing(user_id),
      data: { star_id: starId }
    })
    if (code !== 0) return
    let new_is_following = false
    switch (data.state) {
      case '不可重复关注':
        new_is_following = true
        break
      case 'success':
        new_is_following = !is_following
        break
      default:
        break
    }

    if (is_following && !new_is_following) setFollowSum(sum - 1)
    else if (!is_following && new_is_following) setFollowSum(sum + 1)
    console.log(data)
    changeIsFollow(starId, new_is_following)
  }
  render () {
    return (
      <div className={s.followCard}>
        <div className={s.info}>
          <Avatar size={112} src={this.props.src}></Avatar>
          <div className={s.starName}>{this.props.starName}</div>
        </div>
        <div
          onClick={this.handleFollow}
          style={{
            backgroundColor: this.props.is_following ? '#FFEE90' : '#FFF9D9'
          }}
          className={s.btn}>
            {this.props.is_following ? '已关注' : '未关注'}
        </div>
      </div>
    )
  }
}

function followCardMapStateToProps ({ follow }, ownProps) {
  return {
    ...ownProps,
    sum: follow.sum
  }
}
const ConnectFollowCard = connect(followCardMapStateToProps, actions.follow)(FollowCard)

class AddFollow extends Component {
  constructor(props) {
    super(props)
    this.state = {
      arr: []
    }

    this.componentWillMount = this.componentWillMount.bind(this)
    this.handleBack = this.handleBack.bind(this)
    this.changeIsFollow = this.changeIsFollow.bind(this)
  }
  handleBack () {
    let { history } = this.props
    history.replace('/')
  }
  changeIsFollow (star_id, is_following) {
    let arr = this.state.arr.map(item => {
      item.star_id === star_id
        && (item.is_following = is_following)
      return item
    })
    this.setState({
      arr
    })
  }
  async componentWillMount () {
    await login()
    let user_id = Cookies.get('user_id')
    console.log(user_id)
    let { setFollowSum } = this.props
    let { code, data } = await axios({
      method: 'get',
      url: url.star(user_id)
    })
    if (code !== 0) return
    console.log(data.data)
    let sum = 0
    data.data.forEach(item => {
      item.is_following && sum++
    })
    console.log('sum', sum)
    setFollowSum(sum)
    this.setState({
      arr: data.data
    })
  }
  render () {
    return (
      <div>
        <header className={s.header}>
          <div
            onClick={this.handleBack}
            style={{ backgroundImage: `url(${require('../../static/image/back.png')})` }}
            className={s.back}></div>
          <div>添加关注</div>
          <div className={s.confirm}></div>
        </header>
        <div>
          {this.state.arr.map(item => (
            <ConnectFollowCard
              changeIsFollow={this.changeIsFollow.bind(this)}
              is_following={item.is_following}
              starId={item.star_id}
              starName={item.star_name}
              src={`${window.app.api_domain}/api/image/${item.img}`} key={item.star_id}></ConnectFollowCard>
          ))}
        </div>
      </div>
    )
  }
}

function addFollowMapStateToProps ({ follow }, ownProps) {
  return ownProps
}
const ConnectAddFollow = connect(addFollowMapStateToProps, actions.follow)(AddFollow)

export default ConnectAddFollow



