import React, { Component } from 'react'
import s from './Community.css'
import axios from '../../../api/axios.js'
import url from '../../../api/url.js'
import Cookies from 'js-cookie'

class NewsItem extends Component {
  constructor(props){
    super(props)
    this.post = {
      title: '继续聊一下周琦',
      time: '1分钟前',
      origin: '一片与',
      commentsNum: 112
    }
  }

  render () {
    return (
      <div className={s.post}>
        <p className={s["post_title"]}>{this.post.title}</p>
        <div className={s["post_footer"]}>
          <div className={s["post_info"]}>
            <span className={s["post_time"]}>{this.post.time}</span>
            <span>{this.post.origin}</span>
          </div>
          <div className={s["post_commentsNum"]}><span>评论 {this.post.commentsNum}</span></div>
        </div>
      </div>
    )
  }
}

class Community extends Component {
  constructor(props){
    super(props)

    this.componentWillMount = this.componentWillMount.bind(this)
    this.handlePost = this.handlePost.bind(this)
  }

  handlePost () {
    let { history, match } = this.props
    history.push(`/star/${match.params.star_id}/community/create`)
  }
  async componentWillMount () {
    let { match } = this.props
    console.log(match)
    let user_id = Cookies.get('user_id')
    let { code, data } = await axios({
      method: 'get',
      url: url.starPosts(match.params.star_id, user_id)
    })
    if (code !== 0) return
    console.log(data)
  }

  render () {
    return (
      <React.Fragment>
        <div className={s.community}>
          <NewsItem></NewsItem>
          <NewsItem></NewsItem>
          <NewsItem></NewsItem>
          <NewsItem></NewsItem>
          <NewsItem></NewsItem>
        </div>
        <div className={s.postBtn} onClick={this.handlePost}>发帖</div>
      </React.Fragment>
    )
  }
}

export default Community



