import React, { Component } from 'react';
import imgurl from '../../../../static/image/bang.png';
import s from './TieDetail.css';
import { post } from './data.js'

class Detail extends Component{
  constructor(props){
    super(props);
    console.log(props)
    this.post = post;
    this.state = {upNum: post.upNum};
    this.addUpNum = this.addUpNum.bind(this);
    this.handleBack = this.handleBack.bind(this);
  }

  addUpNum(){
    this.setState((prevState) => ({
      upNum: ++prevState.upNum
    }));
    const form = new FormData();
    const userId = this.getCookie("userId");
    form.append('user_id', userId);
    form.append('post_id', this.post.post_id);
    var xhr = new XMLHttpRequest();
    xhr.open("post",`http://47.95.7.10/${this.post.post_id}/like`,true);
    xhr.send(form);
    xhr.onload = function(){
      if ((xhr.status >=200 && xhr.status <300) || xhr.status === 304){  
        const msg = JSON.parse(xhr.responseText).msg;
        if ( msg === "success"){
          alert("点赞成功");
        }else{
          alert("点赞失败: " + msg);
        }
      }else{
        alert("点赞失败：" + xhr.status);
      }
    };
  }

  getCookie(name){
    const cookies = document.cookie.split(";");
    const key = encodeURIComponent(name) + '='
    let value = null;
    for (let cookie of cookies){
      if (cookie.indexOf(key) > -1){
        value = decodeURIComponent(cookie.slice(key.length));
        break;
      }
    }
    return value;
  }

  handleBack () {
    let { history } = this.props
    history.goBack()
  }

  render(){
    return (
      <div className={s.detail}>
        <div onClick={this.handleBack} className={s.leave}>
          返回
        </div>
        <p className={s["detail_title"]}>{this.post.title}</p>
        <div className={s["user_info"]}>
          <img src={require('../../../../static/image/avatar.png')} className={s.userhead} />
          <span className={s["detail_username"]}>{this.post.userName}</span>
        </div>
        <p className={s["detail_content"]}>{this.post.content}</p>
        <div className={s["detail_footer"]}>
          <span>赞同</span>
          <img src={imgurl} alt="" className={s["up"]} onClick={this.addUpNum} />
          <span>{this.state.upNum}</span>
        </div>
      </div>
    );
  }
}

export default Detail;


